.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card.custom-class {
  background-color: rgb(56, 9, 71);
  margin: 10px;
  width: 80%;
}

.collapsible .content {
  padding: 6px;
}
.collapsible .header {
  background-color: #8e3cdf;
  color: white;
  padding: 6px;
  cursor: pointer;
  width: 400px;
  margin: auto;
}
.collapsible.sub .header {
  background-color: #bc9ade;
  color: black;
  padding: 6px;
  cursor: pointer;
  width: 400px;
  margin: auto;
  padding: 6px;
}
